import React, { Component } from 'react'
import { Carousel, Autocomplete, Datepicker, Dropdown, Modal, FormSelect } from 'materialize-css/dist/js/materialize.min.js'
import HotelCusInfo from './HotelCusInfo'
import VisitorModel from './VisitorModel'

let autocompleteData = {
    "Goa": null,
    "Varanasi": null,
    "Mumbai": null,
    "Delhi": null,
    "Manali": null
}

class TourCaro extends Component {

    state = {
        destination: '',
        origin: '',
        departDate: new Date(),
        hotelType:'',
        hotelAndPassInfo: {
            adults: 1,
            childs: 0,
            infants: 0,
            meal: "BreakFast"
        }
    }

    componentDidMount(){
        if(window.screen.availWidth > 750){
            const flightCaro = document.querySelector('.tour-caro');
            let instance = Carousel.init(flightCaro,{
                fullWidth: true,
                duration: 400
            })

            setInterval(()=>{
                instance.next()
            },6000)
        }

        var fromCity = document.querySelector('.fromCity');
        Autocomplete.init(fromCity,{
            data: autocompleteData,
            limit: 4,
            onAutocomplete: (val)=>{
                this.setState({
                    destination: val
                })
            }
        })

        var toCity = document.querySelector('.toCity');
        Autocomplete.init(toCity,{
            data: autocompleteData,
            limit: 4,
            onAutocomplete: (val)=>{
                this.setState({
                    origin: val
                })
            }
        })


        var departDate = document.querySelector('.departDate');
        Datepicker.init(departDate,{
            defaultDate: new Date(),
            minDate: new Date(),
            onSelect: (selectedDate)=>{
                this.setState({
                    departDate: selectedDate
                });
            }
        })

        var hotelType = document.querySelector('.hotel-type');
        FormSelect.init(hotelType,{

        })

        var flightInfo = document.getElementById('flight-info');
        this.flightDropdown =  Dropdown.init(flightInfo,{
            coverTrigger: false,
            constrainWidth: false,
            closeOnClick: false
        })   

        var userInfo = document.getElementById('user-info');
        this.userDetailsModel =  Modal.init(userInfo,{
            
        })

    }

    handleHotelChangeOption = (e)=>{
        this.setState({
            hotelType: e.target.options[e.target.selectedIndex].value
        })
    }

    getHotelAndPassInfo = (info)=>{
        this.setState({
            hotelAndPassInfo: info
        })
        this.flightDropdown.close();
    }

    submitHotelData = ()=>{
        this.userDetailsModel.open();
    }

    sendAllDataToServer = (userData)=>{
        console.log(userData);
        console.log(this.state);
        this.userDetailsModel.close();
    }

    render(){
        let travellersInfo = this.state.hotelAndPassInfo
        let notravellers = travellersInfo.adults + travellersInfo.childs + travellersInfo.infants; 
        return (
            <div id="booking-activity">
                <div className="carousel carousel-silder default-caro booking-caro tour-caro">
                    <a className="carousel-item" href="#one!">
                        <div className="caro-item" id="caro-1"></div>
                    </a>
                    <a className="carousel-item" href="#two!">
                        <div className="caro-item" id="caro-2"></div>
                    </a>
                    <a className="carousel-item" href="#three!">
                        <div className="caro-item" id="caro-3"></div>
                    </a>  
                </div>

                <div id="flight-model" className="container">
                    <div id="flight-model-content">
                        <div id="flight-iconBox">
                            <i className="material-icons">tour</i>
                            <span>TOURS</span>
                        </div>

                        <div className="row">
                            <div className="col s12 m9">
                                <div className="row">
                                    <div className="input-field col s12 m4">
                                        <i className="material-icons prefix">tour</i>
                                        <input type="text" id="destination" className="autocomplete fromCity" placeholder="Destination"></input>
                                    </div>

                                    <div className="input-field col s12 m4">
                                        <i className="material-icons prefix">location_on</i>
                                        <input type="text" id="to-city-input" className="autocomplete toCity" placeholder="Origin City"></input>
                                    </div>

                                    <div className="input-field col s12 m4">
                                        <i className="material-icons prefix">date_range</i>
                                        <input type="text" className="datepicker departDate" placeholder="Date Of Travel"></input>
                                    </div>
                                </div>
                            
                        <div className="row">
                            <div className="input-field col s12 m4">
                                <select className="hotel-type" onChange={this.handleHotelChangeOption}>
                                    <option value="" disabled selected>Choose Hotel Type</option>
                                    <option value="Budget">Budget/1000+</option>
                                    <option value="Premium">Premium/3000+</option>
                                    <option value="Deluxe">Deluxe/5000+</option>
                                </select>                                
                            </div>

                            <div id="flight-info" className="col s12 m4 dropdown-trigger" data-target='pass-info'>
                                <span>{ notravellers } Travellers, { travellersInfo.meal } </span>
                                <i className="material-icons">person</i>
                            </div> 

                            <HotelCusInfo passInfo={this.getHotelAndPassInfo} />
                        </div>
                        </div>
                        <div className="col s12 m2 offset-m1">
                            <button onClick={this.submitHotelData} className="waves-effect waves-light btn blue" id="search">Search</button>
                        </div>
                        </div>
                    </div>
                </div>
                <VisitorModel sendData={this.sendAllDataToServer} />
            </div>
        )
    }
}

export default TourCaro
